* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


.header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  color: #ffffff;
  background: #24282f;
  &__logo {
    text-transform: uppercase;
    font-size: 26px;
    font-family: 'Raleway', 'Arial';
    font-weight: 400;
  }
}